.about {
  @include pageGrid;

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__description {
    margin: 0;

    @include textFont {
      font-size: 16px;
      font-weight: 400;
      line-height: 140%;
    }

    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }
}
